@media only screen and (max-width: 600px) {
  body { font-size:72.5%; }
}


*{
  font-family: 'Roboto', sans-serif;
  transition: 0.3s all ease-in-out;
    margin: 0;
    padding: 0;
}
body{
  background-color: #efefef;
}
.pictureHeader{
  width: 100%;
  height: 70vh;
  position: absolute;
  top: 0;
  right: 0;
  /*background-color: lavender;*/
  background-image: url('img/headergb.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.overlayHeader{
  width: 100%;
  height: 100%;
  background-color: rgba(39, 46, 59, 0.65);
}
.return{
  position: absolute;
  height: 55px;
  width: 55px;
  padding-left: 15px;
  margin-top: 10vh;
  background-color: #fff;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  border-radius: 2px;
  line-height: 55px;
  font-size: 1.5rem;
  cursor: pointer;
}
.nav{
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
}
.unscrolled{
  height: 10vh;
  background-color: transparent;
  color: #373737;
  box-shadow: none;
}
.scrolled{
  height: 8vh;
  background-color: white;
  color: #373737;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}
.nav ul{
    list-style: none;
    display: flex;
    justify-content: space-around;
}
.unScrolledUl{
  margin: 3vh 10%;
}
.scrolledUl{
  margin: 2vh 10%;
}
.nav li{
    display: inline-block;
}
.nav a{
  text-decoration: none;
  font-size: 1em;
  text-transform: uppercase;
  font-weight: bold;
}
.unScrolledA,.unScrolledA:hover{
  color: #c2c4c7;
}
.scrolledA,.scrolledA:hover{
  color: #373737;
}
.lineNav{
  height: 3px;
  width: 0%;
  margin-top: 3px;
  background-color: #fec107;
  border-radius: 5px;
  transition: 0.2s;
}
li:hover .lineNav{
  width: 100%;
}
.scrollUp{
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: 1px solid #3d4451;
  position: fixed;
  bottom: 30px;
  right: 30px;
  line-height: 45px;
  text-align: center;
  z-index: 100;
}
.scrollUp:hover{
  background-color: #fff;
}
.scrollUp i{
  color: #3d4451;
  z-index: 101;
}
.noDisplay{
  opacity: 0;
}
/*_____________________________________MAIN CARD__________________________________________*/
.mainCard{
  position: relative;
  padding: 20vh 0 5vh;
}

.cardLeaf{
  background-color: white;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}
.mainInfo{
  padding: 60px 5% 0;
}
.avatar{
  width: 90%;
  height: 350px;
  margin: 0 auto 40px;
}
.pic{
  height: 100%;
  width: 100%;
  background-image: url("./img/avatar.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.contacts{
  width: 100%;
  height: 85px;
  background-color: #fec107;
  display: flex;
  justify-content: center;
  padding: 20px 20px;
}
.chipHello{
  width: 70px;
  height: 30px;
  background-color: #fec107;
  border-radius: 20px;
  color: white;
  text-align: center;
  margin-bottom: 25px;
}
.chipHello h5{
  line-height: 29px;
}
.greeting h2, .greeting h4{
  font-weight: lighter;
  color: #373737;
  margin-bottom: 0;
}
.greeting span{
  font-weight: bold;
}
.lineMain{
  height: 1px;
  width: 100%;
  background-color: #c7c7c7;
  margin: 25px 0;
}
.moreInfo p{
  margin-bottom: 10px;
}
.parameter{
  float: left;
  width: 25%;
  color: #373737;
  font-weight: bold;
  margin-bottom: 40px;
  text-transform: uppercase;
}
.value{
  float: right;
  width: 75%;
  color: #b9b9b9;
}

.icon{
  height: 45px;
  width: 45px;
  margin: 0 5%;
  border-radius: 50%;
  background: transparent;
  text-align: center;
  line-height: 45px;
}
.icon:hover{
  background: rgba(0, 0, 0, 0.22);
}
.icon a{
  color: white;
  font-size: 20px;
}

/*_____________________________SKILLS______________________*/

#skills{
  padding-top: 30px;
}
.sectionHeader{
  margin: 50px 0 30px;
  color: #3d4451;
}
.downloadResume{
  position: relative;
  top: 0;
  height: 60px;
  width: 250px;
  color: #3d4451;
  font-weight: bold;
  background-color: transparent;
  border: 1px #cbcdcf solid;
  border-radius: 4px;
  text-transform: uppercase;
  cursor: pointer;
}
.downloadResume:focus{
  outline: none;
}
.downloadResume:hover{
  box-shadow: 0 12px 38px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}
.skillsCard{
  padding: 30px 5%;
}
.progress{
  background-color: rgba(254, 193, 7, 0.25);
  height: 6px;
  width: 85%;
}
.progress-bar{
  width: 0;
}
.progressJs{
  width: 80%;
}
.progressHtml{
  width: 95%;
}
.progressCss{
  width: 85%;
}
.progressJq{
  width: 85%;
}
.progressAng{
  width: 65%;
}
.progressReact{
  width: 35%;
}
.progress-bar{
  background-color: #fec107;
}
#skills h5{
  font-size: 1rem;
  color: #3d4451;
  font-weight: lighter;
}
.skillsCard .col-6{
  margin: 20px 0;
}

.fade-out {
  opacity: 0;
}
.visible {
  transition: 0.2s ease-in;
  opacity: 1;
}

/*__________________________________WORKS____________________________________*/
.worksCards{
    display: flex;
    justify-content: space-around;
  flex-wrap: wrap;
}
.workCard{
  height: 230px;
    position: relative;
    border-radius: 3px;
    background-color: #fff;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  margin-top: 20px;
}
.workCard i{
  width: 100%;
  margin-top: 25px;
  text-align: center;
  color: #fec107;
  font-size: 5rem;
}
.workCard img{
  height: 85px;
  /*margin-top: 25px;*/
  display: block;
  margin: 20px auto 0;
}
.mainInfoWork{
  position: absolute;
  display: block;
  bottom: 0;
  width: 100%;
    padding: 10px 20px 10px;
  text-align: center;
}
.workCard h2{
    margin: 0;
}
.workCard span{
    color: #3d4451;
    font-size: 15px;
}
.lineWork{
  width: 100%;
  height: 4px;
  background-color: #fec107;
}
#works a{
  color: #3d4451;
  text-decoration: none;
}

/*_______________________________INTERESTS______________________________*/

#interests{
  padding: 20px 0;
}
#interests .cardLeaf{
  padding: 40px 5%;
}
.boxInt{
  width: 80px;
  height: 80px;
  border: 1px #cbcdcf solid;
  border-radius: 4px;
  margin: 20px 15px;
  line-height: 90px;
  text-align: center;
}
.boxInt i{
  font-size: 2rem;
  color:#fec107;
}


#photos{
  padding: 20px 0;
}
.mainCar{
  width: 62%;
  margin-right: 1.5%;
}
.otherCar{
  width: 30%;
}
#carouselMain{
  width: 100%;
  border: 1px #cbcdcf solid;
  border-radius: 4px;
}
#carouselMainTwo{
  width: 100%;
  margin-bottom: 6.5%;
  border: 1px #cbcdcf solid;
  border-radius: 4px;
}
#carouselMainThree{
  width: 100%;
  border: 1px #cbcdcf solid;
  border-radius: 4px;
}
#carouselMain:hover, #carouselMainTwo:hover, #carouselMainThree:hover{
  box-shadow: 0 12px 38px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}
.carousels{
  display: flex;
  flex-wrap: wrap;
}


/*___________________CONTACT_ME_____________*/
#contactMe{
  padding: 20px 0 40px;
  color: #3d4451;
  position: relative;
}
#contactMe .cardLeaf{
  padding: 40px;
}
#contactMe h3{
  margin-bottom: 25px;
}
#contactMe input[type=text], input[type=number], #contactMe textarea{
  width: 100%;
  background-color: transparent;
  border: none;
  border-bottom: 1px #999 solid;
  margin-bottom: 10px;
  resize: none;
  height: 1.9rem;
}
form>div{
  height: 60px;
}
form{
  padding-top: 5px;
}
input:focus, textarea:focus, button:focus{
  outline: none;
}
.labelUp{
  position: relative;
  font-weight: normal;
  top:-65px;
  font-size:12px;
  color:#fec107;
  text-transform: uppercase;
}
.inputNotEmpty{
  font-weight: normal;
  position:relative;
  top:-65px;
  font-size:12px;
  pointer-events:none;
  transition:0.2s ease all;
  text-transform: uppercase;
}
.inputEmpty{
  color: #3d4451;
  font-size:15px;
  font-weight:lighter;
  position:relative;
  pointer-events:none;
  top: -45px;
  transition:0.2s ease all;
  text-transform: uppercase;
}
.green{
  color:#fec107;
}
.red{
  color: red;
}
.redBar:before, .redBar:after{
  content:'';
  height:3px;
  width:0;
  bottom: 10px;
  position:absolute;
  background: red;
  transition:0.2s ease all;
}
.bar{
  position:relative;
  display:block;
  width:100%;
}
.yellowBar:before, .yellowBar:after{
  content:'';
  height:3px;
  width:0;
  bottom: 10px;
  position:absolute;
  background:#fec107;
  transition:0.2s ease all;
}
.bar:before {
  left:50%;
}
.bar:after {
  right:50%;
}
.mess .bar{
  bottom: 5px;
}
#contactMe input:focus ~ .bar:before, input:focus ~ .bar:after {
  width:50%;
}
#contactMe textarea:focus ~ .bar:before, textarea:focus ~ .bar:after {
  width:50%;
}
#contactMe button{
  width: 150px;
  height: 50px;
  background-color: #fec107;
  text-transform: uppercase;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 4px;
}
.buttonShake{
  animation: shake 0.5s;
  animation-iteration-count: 1;
}
@keyframes shake {
  0% { transform: translate(1px, 1px) rotate(0deg); }
  10% { transform: translate(-1px, -2px) rotate(-1deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  30% { transform: translate(3px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(1deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(3px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(1px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}
#contactMe button:hover{
  box-shadow: 0 10px 25px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

fieldset >div{
  height: 40px;
  width: 70px;
  display: inline-block;
  margin-bottom: 5px;
  text-transform: uppercase;
  font-weight: lighter;
}
input[type=checkbox], input[type=radio] {
  height: 25px;
  float: left;
  margin-right: 5px;
}
.replyMe{
  text-transform: uppercase;
  font-weight: lighter;
  height: 40px;
}
.letter{
  position: absolute;
  top: 200px;
  width: 100%;
  height: 70vh;
}
.letterHidden{
  z-index: -10;
  opacity: 0;
}
.letterShown{
  z-index: 10;
  opacity: 1;
}
.letter h1{
  text-align: center;
}
.letterInfo div{
  margin-bottom: 5px;
  display: block;
}
.key{
  width: 30%;
  float: left;
}
.value{
  width: 70%;
  float: right;
}
/*____________FOOTER__________________________*/

#footer{
    padding-top: 7vh;
    height: 15vh;
    text-align: center;
    color: #cbcdcf;
}
